<template>
  <div id="console">
    <form
      class="flex justify-center"
      :class="{ hidden: loading }"
      @submit.prevent="classify"
    >
      <input
        type="text"
        class="form-control rounded-md w-3/4"
        v-model="sentence"
        placeholder="Input Sentence to be Classified"
      />
      <button type="submit" class="text-black -ml-10">
        <b-icon icon="arrow-right" font-scale="2" aria-hidden="true"></b-icon>
      </button>
    </form>
    <Loader :visible="loading" />
  </div>
</template>
<script>
import Loader from "./loader"
import { serverUrl } from '../config';

export default {
    data(){
        return {
            sentence:"",
            loading:false
        }
    },
    methods:{
        classify(){
            if (this.sentence) {
                this.loading = !this.loading;
                fetch(`${serverUrl}/classify?sentence=${this.sentence}`, {
                }).then(res => res.json())
                .then(res => {
                    this.$notify({
                        group: 'foo',
                        type: res.info || res.error,
                        title: res.info,
                        duration: 10000,
                        text: res.info ? `Polarity : ${res.results.polarity} <br> Polarity Score : ${res.results.score}` : res.error
                    })
                    this.loading = false;
                })
                .catch(e => {
                    this.$notify({
                        group: 'foo',
                        type: 'error',
                        title: `<h2>${e}</h2>`
                    });
                    this.loading = false;
                });
                this.sentence = "";
            } else {
                this.$notify({
                group: "foo",
                type: "warn",
                title: "<h2>Sentence cannot be empty</h2>",
                });
            }
        },
    },
    components:{
        Loader
    }
}
</script>