<template>
  <div class="min-h-screen max-h-screen bg-fixed flex items-center justify-center">
    <div class="container">
      <div class="mx-auto box-content pb-4">
        <h1
          class="text-center text-6xl font-sans text-white animate__animated animate__pulse animate__slow"
        >
          REAL TIME SENTIMENT ANALYSIS
        </h1>
        <div class="flex justify-center m-3 p-2">
            <span class="hover:transform hover:scale-75">
                <b-icon @click="showComponentButtons = true" class="text-white" icon="arrow-down-circle" font-scale="2" aria-hidden="true"></b-icon>
            </span>
        </div>
        <notifications group=foo position="top center"/>
        <transition
          enter-active-class="animate__animated animate__fadeInDown"
          leave-active-class="animate__animated animate__fadeOutUp"
        >
          <section v-show="showComponentButtons">
            <div
              class="flex justify-center m-3 p-2"
              :class="{ 'space-x-10': !showStream || !showConsole }"
            >
              <transition
                enter-active-class="animate__animated animate__fadeInLeft"
                leave-active-class="animate__animated animate__fadeOutLeft"
              >
                <b-button
                  v-show="!showConsole"
                  v-b-popover.hover.left="'Handling Real Time Twitter Data'"
                  @click="toggleStream"
                  class=""
                >
                  <b-icon
                    icon="twitter"
                    aria-hidden="true"
                    font-scale="2"
                  ></b-icon>
                </b-button>
              </transition>
              <transition
                enter-active-class="animate__animated animate__fadeInRight"
                leave-active-class="animate__animated animate__fadeOutRight"
              >
                <b-button
                  v-show="!showStream"
                  v-b-popover.hover.right="
                    'Classify a sentence using pretrained model'
                  "
                  @click="toggleConsole"
                  class=""
                >
                  <b-icon
                    icon="terminal-fill"
                    aria-hidden="true"
                    font-scale="2"
                  ></b-icon>
                </b-button>
              </transition>
            </div>
            <p
              class="text-white text-center animate__animated animate_fadeInUp -mt-1"
              v-show="showConsole || showStream"
            >
              (Click again to go back)
            </p>
          </section>
        </transition>
      </div>
      <div class="container mb-3">
        <transition-group
          mode="out-in"
          enter-active-class="animate__animated animate__fadeInUp animate__slow"
          leave-active-class="animate__animated animate__fadeOutDown animate__slow"
        >
          <Stream :key="1" v-show="showStream" />
          <Terminal :key="2" v-show="showConsole"></terminal>
        </transition-group>
      </div>
    </div>
  </div>
</template>
<script>
import Stream from "./Stream";
import Terminal from "./Terminal";

export default {
  name: "Main",
  data() {
    return {
      showStream: false,
      showConsole: false,
      showComponentButtons: false,
    };
  },
  methods: {
    toggleStream() {
      this.showStream = !this.showStream;
      this.showConsole = false;
    },
    toggleConsole() {
      this.showConsole = !this.showConsole;
      this.showStream = false;
    },
  },
  components: {
    Stream,
    Terminal
  },
};
</script>
<style scoped>
.bg-fixed {
  background-image: url("~@/assets/images/bg.jpg");
  background-size: cover;
}
.container {
  transition: height 2s;
}
</style>
