<template>
  <div id="stream">
    <section class="controller">
      <ul class="flex items-center justify-center space-x-6">
        <li>
          <a
            @click="
              () => {
                initStream = true;
                showDirectory = false;
                loading = false;
              }
            "
            class="btn btn-primary"
            >Create New Stream</a
          >
        </li>
        <li>
          <a @click="viewPrevious" class="btn btn-primary"
            >View Previous Stream</a
          >
        </li>
      </ul>
    </section>
    <loader :visible="loading" />
    <transition-group
      mode="out-in"
      enter-active-class="animate__animated animate__fadeInLeft"
      leave-active-class="animate__animated animate__fadeOutRight"
    >
      <section v-show="initStream" class="pt-2" :key="3">
        <form
          class="flex justify-center"
          :class="{ hidden: loading }"
          @submit.prevent="createStream"
        >
          <input
            type="text"
            v-model="keyword"
            class="form-control rounded-md w-50"
            placeholder="Stream Keyword(s)"
          />
          <button type="submit" class="text-black -ml-10">
            <b-icon
              icon="arrow-right"
              font-scale="2"
              aria-hidden="true"
            ></b-icon>
          </button>
        </form>
      </section>
      <section v-show="showDirectory" class="p-3" :key="1">
        <div class="overflow-x-auto card p-2">
          <table>
            <thead>
              <tr class="font-md">
                <td>File name</td>
                <td>Date Created</td>
                <td>Size</td>
              </tr>
            </thead>
            <tbody>
              <tr
                class="p-1"
                :key="index"
                v-for="(file, index) in previousStreamFiles"
              >
                <td>
                  <a :href="`${serverUrl}/file/${file.name}`">
                    <b-icon
                      variant="primary"
                      icon="file-earmark-arrow-down-fill"
                    />
                    {{ file.name }}
                  </a>
                </td>
                <td>{{ file.created_at }}</td>
                <td>{{ formatFileSize(file.size) }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </section>
      <section
        style="height:350px !important;"
        v-show="serverConsole"
        :key="2"
        class="mx-auto bg-gray-100 my-3"
      >
        <div class="flex flex-row h-full">
          <div class="w-11 bg-gray-500">
            <b-button
              @click="terminate"
              v-b-popover.hover.top="'Click to stop currennt execution'"
            >
              <b-icon
                aria-hidden="true"
                icon="stop-circle-fill"
                animation="pulse"
              ></b-icon>
            </b-button>
          </div>
          <!-- <ul id="scrollable" class="p-2 overflow-auto" style="width:1175px"> -->
          <ul id="scrollable" class="p-3 overflow-auto" style="">
            <li
              class="my-2 px-2 text-sm"
              v-for="(text, index) in sConsoleData"
              :key="index"
            >
              {{ text }}
            </li>
          </ul>
        </div>
      </section>
    </transition-group>
  </div>
</template>
<script>
import loader from "./loader";
import { serverUrl } from "../config";

export default {
  name: "Stream",
  created() {
    window.train = () => {
      const b = new EventSource(serverUrl + '/train');
      b.onmessage =  (stream) => {
        this.handleStream(b, stream)
      }
    }
    window.test = () => {
      const d = new EventSource(serverUrl + '/test');
      d.onmessage =  (stream) => {
        this.handleStream(d, stream)
      }
    }
  },
  data() {
    return {
      keyword: "",
      initStream: false,
      showDirectory: false,
      loading: false,
      serverConsole: false,
      previousStreamFiles: [],
      sConsoleData: [],
      serverUrl
    };
  },
  components: {
    loader,
  },
  methods: {
    formatFileSize(bytes) {
      let kb = Number(bytes) / 1024;
      if (kb >= 0 && kb <= 0.999999) {
        return bytes.toString().slice(0, 5) + " Bytes";
      }
      if (kb >= 1 && kb < 1000) {
        return kb.toString().slice(0, 5) + " KB";
      } else {
        kb /= 1024;
        return kb.toString().slice(0, 5) + " MB";
      }
    },
    createStream() {
      if (this.keyword) {
        this.loading = true;
        const a = new EventSource(
          `${serverUrl}/stream?keyword=${this.keyword}`
        );
        if (this.serverConsole.length > 0) this.serverConsole = [];
        a.onmessage = (stream) => this.handleStream(a, stream);
        a.onerror = (error) => {
          error
          this.$notify({
            group: "foo",
            type: "error",
            title: "Stream Error",
            text: `<h4>Destination unreachable</h4>`,
          });
        };
        setTimeout(() => {
          if (a.readyState != 1) {
            a.close();
            this.$notify({
              group: "foo",
              type: "error",
              title: "Stream Error",
              text: `<h4>Unable to establish connection</h4>`,
            });
            if (this.loading) this.loading = false;
          }
        }, 15000);
        this.keyword = "";
      }
    },
    viewPrevious() {
      this.initStream = false;
      this.loading = true;
      this.serverConsole = false;
      fetch(`${serverUrl}/prevStream`)
        .then((res) => res.json())
        .then((files) => {
          this.showDirectory = true;
          this.loading = false;
          if (this.previousStreamFiles.length == 0) {
            files.forEach((e) => {
              this.previousStreamFiles.push(e);
            });
          }
        })
        .catch((err) => {
          this.$notify({
            group: "foo",
            type: "error",
            duration: 10000,
            title: "Error fetching resource",
            text: err,
          });
          this.loading = false;
        });
    },
    handleStream(e, s) {
      let { data } = s;
      data = JSON.parse(data);
      
      // this.loading = false;
      if (data.execution) {
        this.sConsoleData.push(data.response);
        this.$el.querySelector("#scrollable").scrollTop = this.$el.querySelector("#scrollable").scrollHeight;
        this.serverConsole = true;
      } else if (data.info) {
        this.$notify({
          type: "info",
          group: "foo",
          duration: 10000,
          title: "Execution Info",
          text: data.info,
        });
        e.close();
        this.loading = false;
      } else if (data.error) {
        this.$notify({
          type: "error",
          group: "foo",
          duration: 10000,
          title: "Execution Error",
          text: data.error,
        });
        e.close();
        this.loading = false;
      }
      else {
        e.close();
        this.loading = false
      }
    },
    terminate() {
      fetch(`${serverUrl}/terminate`)
        .then((res) => res.json())
        .then((res) => {
          this.loading = false;
          this.$notify({
            type: "info",
            group: "foo",
            title: "Termination Info",
            text: res.info,
          });
        })
        .catch((err) => console.error(err));
    },
  },
};
</script>
